$black-color: #000000;
$white-color: #ffffff;
$blue-1-color: #81b3d2;
$blue-2-color: #669ec0;
$blue-3-color: #5496bd;
$light-gray: #eeeeee;
$background-gray-color: #f2f2f2;
$shadow-color: rgba(0, 0, 0, 0.2);
$font-family: "PT Sans", "Arial", sans-serif;
$text-gray-color: #333333;

body {
  font-weight: normal;
  font-size: 14px;
  line-height: 21px;
  font-family: $font-family;
  color: $black-color;
  text-transform: uppercase;

  background-color: $background-gray-color;
}

.hide {
  display: none;
}

%clearfix {
  &::after {
    content: "";

    display: table;
    clear: both;
  }
}

%shadow {
  box-shadow: 0 0 20px $shadow-color;
}

.main {
  @extend %shadow;

  min-width: 768px;
  max-width: 1200px;
  margin: 0 auto;
}

@import "global/btn";
@import "global/sprite";

@import "global/top-panel";
@import "global/footer";

@import "index";
@import "catalog";

// костыли для лучшего соответствия макету
img[src*="souvenirs"] {
  transform: translate(0, -4px);
}

@media (min-width: 950px) {
  .filter-range .field-title {
    margin-left: -10px;
  }

  .btn-transparent {
    margin-left: 84px;
  }
}

@media (max-width: 800px) {
  .main-header {
    background-size: 100%;
  }

  .feature--big .feature__name {
    margin-top: 47px;
  }

  .feature--big .feature__description {
    width: 75%;
  }

  .range-controls {
    margin-left: 5px;
  }

  .range-toggle-max {
    left: 71%;
  }

  .filter-range .field-title {
    margin-left: 5px;
  }
}
